<template>
  <section class="All-News">
    <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mx-1 max-w-screen-lg">
      <div  class="w-full h-auto">
        <div v-for="items in menu" :key="items.news_id">
          <div v-for="value in items.news" :key="value.news_id">
            <div class="flex flex-col space-y-1">
              <div class="flex flex-row align-center justify-between w-full">
                <div class="w-7/12">
                  <span id="message" class="text-black font-thin" style="font-size:11px;height: 80px">{{value.title}}...</span>
                </div>
                <div class="w-4/12"><v-lazy-image @error="notShow" :src="`${value.news_image[0].url}`" alt="" class="w-full h-14 lg:w-full lg:h-14 sm:w-8/12 sm:h-16 rounded-md"/></div>
              </div>
              <div class="flex flex-row align-center justify-between w-full sm:w-9/12">
                <div><p class="text-slate-600 font-extrabold" style="font-size: 10px">3 دقیقه قبل</p></div>
                <div>
                  <font-awesome-icon :icon="['fas','comment']" class="text-slate-600 ml-1" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-bold" style="font-size: 10px">{{value.news_comment_count}}</span>
                  <font-awesome-icon :icon="['fas','eye']" class="text-slate-600 ml-1" style="font-size: 10px"></font-awesome-icon>
                  <span class="text-slate-600 font-bold" style="font-size: 10px">{{value.visit}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import VLazyImage from "v-lazy-image";
import{ref} from 'vue'
export default {
  name: "SideLastNews",
  components:{FontAwesomeIcon,VLazyImage},
  props:{
    menu:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
notFound,notShow
    }
  }
}
</script>

<style scoped>
#message{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space: normal;
}
</style>